<template>
  <div>
    <h2>اخبار من</h2>
    <div v-if="loading">در حال بارگذاری...</div>
    <div v-else>
      <div v-if="newsList.length === 0">شما هنوز خبری ثبت نکردید.</div>
      <ul>
        <li v-for="news in newsList" :key="news.id" class="news-item">
          <h3>{{ news.title }}</h3>
          <p>{{ news.description }}</p>
          <small>دسته‌بندی: {{ news.category_title || 'ندارد' }}</small>
          <div class="actions">
            <button @click="editNews(news.id)">ویرایش</button>
            <button @click="deleteNews(news.id)">حذف</button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { sendApi } from '@/utils/api'
import router from '@/router'

const newsList = ref([])
const loading = ref(false)

const loadNews = async () => {
  loading.value = true
  try {
    const res = await sendApi({ control: 'news', action: 'user_news' })
    if (res.status === 'success') {
      newsList.value = res.data || []
    } else {
      alert('خطا در بارگذاری اخبار: ' + res.message)
    }
  } catch (error) {
    alert('خطا در ارتباط با سرور: ' + error.message)
  }
  loading.value = false
}

const editNews = (id) => {
  // فرض میکنیم صفحه ویرایش داریم با روت مثل: /news/edit/:id
  router.push(`/news/edit/${id}`)
}

const deleteNews = async (id) => {
  if (!confirm('آیا مطمئن هستید که می‌خواهید این خبر را حذف کنید؟')) return
  try {
    const res = await sendApi({ control: 'news', action: 'delete_news', data: { news_id: id } })
    if (res.status === 'success') {
      alert('خبر با موفقیت حذف شد.')
      await loadNews()
    } else {
      alert('خطا در حذف خبر: ' + res.message)
    }
  } catch (error) {
    alert('خطا در ارتباط با سرور: ' + error.message)
  }
}

onMounted(() => {
  loadNews()
})
</script>

<style scoped>
.news-item {
  border-bottom: 1px solid #ccc;
  padding: 1rem 0;
}
.actions button {
  margin-right: 0.5rem;
}
</style>
